<template>
  <div class="con">
    <publicTop></publicTop>
    <div class="home-top">
      <div class="home-top-l" @click="goB">
        <img src="@/assets/left-le.png" />
      </div>
      <div class="home-top-r">
        <!-- <img
          src="@/assets/icon_collect.png"
          class="home-top-r-img1"
          v-if="!contentDetail.flag"
        />
        <img src="@/assets/icon_collected.png" class="home-top-r-img1" v-else /> -->
        <!-- <img
          src="@/assets/icon_share.png"
          class="home-top-r-img2"
          @click="goShare"
        /> -->
        详情
      </div>
    </div>

    <div class="home-body">
      <div class="home-body-t">
        {{ contentDetail.article.title }}
      </div>
      <div class="list-all-li-end">
        <div class="list-all-li-end-l">
          来源于{{ contentDetail.article.publisher }}
          <span>{{ contentDetail.article.hits }}次浏览数</span>
        </div>
        <div class="list-all-li-end-r">
          {{ contentDetail.article.releaseDate.slice(0, 10) }}
        </div>
      </div>
      <div
        class="home-body-content"
        v-html="contentDetail.article.articleData.content"
      ></div>
    </div>
  </div>
</template>

<script>
import qs from "qs";
import publicTop from "@/components/publicTop.vue";
export default {
  name: "newsDetail",
  components: {
    publicTop,
  },
  data() {
    return {
      dec: "北京林业大学",
      contentDetail: {},
      newsId: "",
    };
  },
  methods: {
    goB() {
      this.$router.go(-1);
    },
    getDetailContent() {
      const params = {
        id: this.newsId,
      };
      console.log(params);
      this.$axios
        // .get("/f/newsCenter/ajax_view?id=" + this.newsId)
        .post("/f/newsCenter/ajax_view", qs.stringify(params))
        .then((res) => {
          console.log(res, "详情");
          if (res.data.state == 1) {
            this.contentDetail = res.data.object;
            this.contentDetail.article.articleData.content =
              this.contentDetail.article.articleData.content.replace(
                /<img/g,
                "<img style='max-width:100%;height:auto;'"
              );
          } else {
            this.contentDetail = {};
          }
        })
        .catch((err) => {
          console.log("获取失败");
        });
    },
    getUrl(paraName) {
      var url = document.location.toString();
      console.log(url);
      var arrObj = url.split("?");
      if (arrObj.length > 1) {
        var arrPara = arrObj[1].split("&");
        var arr;
        console.log(arrPara);
        for (var i = 0; i < arrPara.length; i++) {
          arr = arrPara[i].split("=");
          console.log(arr);
          if (arr != null && arr[0] == paraName) {
            return arr[1];
          }
        }
      } else {
        return "";
      }
    },
  },
  created() {
    // console.log(this.$store.getters.getCOMPONENTS, "11");
    // console.log(this.$store.getters.getNewsId, "89");
    // this.newsId = this.$store.getters.getNewsId;
    console.log(this.$route.query.id, 9, this.getUrl("id"), 8);
    this.newsId = this.$route.query.id;
    this.getDetailContent();
  },
  beforeDestory() {},
  destroyed() {},
};
</script>
<style lang="css" scoped>
.con {
  background-color: #f5f5f5;
  padding-bottom: 0.66rem;
  min-height: 100%;
}
.home-top {
  width: 7.5rem;
  padding: 0rem 0.4rem;
  box-sizing: border-box;
  height: 0.88rem;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  background-color: #ffffff;
  border-bottom: 0.01rem solid #eeeeee;
}
.home-top-l {
  display: flex;
  align-items: center;
  width: 2rem;
  height: 0.88rem;
  position: absolute;
  top: 0;
  left: 0.4rem;
}
.home-top-l img {
  width: 0.2rem;
  height: 0.3rem;
}

.home-top-r {
  font-family: PingFangSC-Semibold;
  font-size: 0.32rem;
  font-weight: bold;
  font-stretch: normal;
  letter-spacing: 0rem;
}

.home-top-r-img1 {
  width: 0.4rem;
  height: 0.36rem;
  margin-right: 0.44rem;
}

.home-top-r-img2 {
  width: 0.36rem;
  height: 0.38rem;
}
.home-body {
  width: 7.5rem;
  padding: 0.4rem 0.32rem;
  box-sizing: border-box;
  background-color: #ffffff;
}
.home-body-t {
  width: 100%;
  font-family: PingFangSC-Medium;
  font-size: 0.32rem;
  font-weight: bold;
  font-stretch: normal;
  letter-spacing: 0rem;
  color: #2b2e34;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  word-break: break-all;
  text-align: left;
  height: 0.85rem;
}
.home-body-content {
  width: 6.86rem !important;
  text-align: left;
  font-family: PingFangSC-Medium;
  font-size: 0.28rem;
  font-weight: normal;
  font-stretch: normal;
  line-height: 0.44rem;
  letter-spacing: 0rem;
  color: #7a7c8b;
  text-align: justify;
  padding-top: 0.32rem;
  box-sizing: border-box;
}
.home-body-content >>> img {
  max-width: 100% !important;
  height: auto;
}
.home-body-content >>> table {
  max-width: 100% !important;
  height: auto;
}
.home-body-content >>> p > span {
  width: 6.86rem !important;
  height: auto;
  word-break: break-all;
  white-space: normal;
}
.home-body-content >>> p > span > span > a {
  display: inline-block;
  width: 6.86rem;
  overflow-x: scroll;
  word-break: break-all;
  white-space: normal;
}
.home-body-content >>> p > span > span {
  word-break: break-all;
  white-space: normal;
}
.home-body-content >>> p > img {
  max-width: 6rem !important;
  height: auto;
}
.home-body-content >>> p > span > a > span {
  word-break: break-all;
  white-space: normal;
}
.home-body-content >>> p {
  max-width: 6.86rem !important;
  height: auto;
  margin-left: 0 !important;
  text-indent: 0 !important;
}
.home-body-content >>> p a {
  word-break: break-all;
  white-space: normal;
}
.home-body-content >>> table {
  max-width: 100% !important;
  height: auto;
  display: inline-block;
  table-layout: fixed;
}
.home-body-content >>> table tbody {
  max-width: 100% !important;
  height: auto;
  display: inline-block;
}
.home-body-content >>> table tbody tr {
  max-width: 100% !important;
  height: auto;
  display: inline-block;
  overflow-x: scroll;
}
.home-body-content >>> p > img {
  max-width: 100% !important;
  height: auto;
  overflow-x: scroll;
}
.home-body-content >>> p > u {
  width: 6rem !important;
  height: auto;
  display: inline-block;
  overflow-x: scroll;
}

.list-all-li-end {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 0.18rem;
}
.list-all-li-end-l {
  font-family: PingFangSC-Regular;
  font-size: 0.24rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0rem;
  color: #b3b3b3;
}
.list-all-li-end-l span {
  display: inline-block;
  margin-left: 0.2rem;
}
.list-all-li-end-r {
  font-family: PingFangSC-Regular;
  font-size: 0.24rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0rem;
  color: #b3b3b3;
}
</style>


